/* stylelint-disable property-no-vendor-prefix */
:host {
  @include reset-host;
  padding: 2rem 0;
  overflow: hidden;
}

:host .teaser-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

:host .teaser-navigation {
  display: flex;
  gap: rem(12);

  @include mq('max', 'md') {
    display: none;
  }
}

:host .teaser-slider {
  display: flex;
  gap: rem(12);
  margin: rem(32) 0;
  overflow: auto;

  @include mq('min', 'md') {
    gap: rem(24);
    padding-bottom: rem(32);
    overflow: auto;
  }

  &::-webkit-scrollbar-track {
    background-color: transparent;
  }

  &::-webkit-scrollbar {
    height: 2px;
    background-color: transparent;

    @include mq('max', 'md') {
      display: none;
    }
  }

  &::-webkit-scrollbar-thumb {
    background-color: $primary;
  }
}

:host .navigation-button {
  padding: rem(8);
  font-size: rem(24);
  border: 1px solid $gray-800;
  border-radius: 50%;

  &:focus {
    background-color: $gray-800;
    outline: 0;
  }
}

:host .teaser-card {
  flex-grow: 1;
}

:host .progress-bar {
  width: 100%;
  height: rem(2);
}

:host .progress-indicator {
  width: 0;
  height: 100%;
  background-color: $primary;
}

:host .grow {
  flex-grow: 1;
}

:host .click-action {
  outline: 0;
}
