import { Component, Host, h, Prop, State, Listen } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { BsTitle } from '../../atoms/title/title';
import { ExperienceZone, ExperienceZoneLayout } from './experience-zone.types';
import { IconName } from '../../atoms/icon/icon.types';
import { throttle } from '../../../utils/throttle';

/**
 * Experience Zone teaser component.
 */
@Component({
  tag: 'bs-experience-zone',
  styleUrl: 'experience-zone.scss',
  shadow: true,
})
export class BsExperienceZoneTeaser {
  private teaserSliderRef: HTMLDivElement;

  /** States */
  @State() slides: boolean = false;

  @State() activeIndex: number = 0;

  /** Props */
  /**
   * The Experience Zone contract.
   */
  @Prop() experienceZoneData: ExperienceZone | string;

  @Memoize('experienceZoneData')
  get parsedExperienceZoneData(): ExperienceZone {
    return parseIfString(this.experienceZoneData);
  }

  /**
   * The Experience Zone Teaser title.
   */
  @Prop() teaserTitle: BsTitle | string;

  @Memoize('teaserTitle')
  get parsedTeaserTitle(): BsTitle {
    return parseIfString(this.teaserTitle);
  }

  /** listeners */
  @Listen('resize', { target: 'window', passive: true })
  handleScreenResized() {
    this.throttleMethod();
  }

  /** Methods */
  private onResize = () => {
    this.slides =
      this.teaserSliderRef && this.teaserSliderRef.scrollWidth > this.teaserSliderRef.offsetWidth;
  };

  private throttleMethod = throttle(this.onResize, 1000);

  private onPreviousSlide = () => {
    if (this.activeIndex < 1) return;
    if (this.teaserSliderRef.scrollLeft === 0) return;
    this.activeIndex -= 1;

    this.scrollSlider();
  };

  private onNextSlide = () => {
    if (this.activeIndex > this.parsedExperienceZoneData.stories.length - 2) return;
    if (
      this.teaserSliderRef.scrollLeft + this.teaserSliderRef.clientWidth ===
      this.teaserSliderRef.scrollWidth
    )
      return;
    this.activeIndex += 1;

    this.scrollSlider();
  };

  private scrollSlider = () => {
    const scrollDistance =
      (this.teaserSliderRef.children[this.activeIndex] as HTMLBsClickActionElement).offsetLeft -
      this.teaserSliderRef.offsetLeft;

    this.teaserSliderRef.scrollTo({
      top: 0,
      left: scrollDistance,
      behavior: 'smooth',
    });
  };

  /** lifecycle hooks */
  componentDidLoad() {
    this.slides = this.teaserSliderRef.scrollWidth > this.teaserSliderRef.offsetWidth;
  }

  render() {
    return (
      <Host>
        <div class="teaser-head">
          <bs-title
            text={this.parsedTeaserTitle.text}
            tag={this.parsedTeaserTitle.tag}
            size={this.parsedTeaserTitle.size}
            variant={this.parsedTeaserTitle.variant}
            color={this.parsedTeaserTitle.color}
          />
          {this.slides && (
            <div class="teaser-navigation">
              <bs-click-action
                name="previous"
                onBsClickAction={this.onPreviousSlide}
                tagClass="navigation-button"
                ariaLabelText="backward"
              >
                <bs-icon name={IconName.ArrowBackward} />
              </bs-click-action>
              <bs-click-action
                name="next"
                onBsClickAction={this.onNextSlide}
                tagClass="navigation-button"
                ariaLabelText="forward"
              >
                <bs-icon name={IconName.ArrowForward} />
              </bs-click-action>
            </div>
          )}
        </div>
        <div
          class="teaser-slider"
          ref={(el) => {
            this.teaserSliderRef = el;
          }}
        >
          {this.parsedExperienceZoneData.stories.map((story, index) => (
            <bs-click-action
              name="open-experience-zone-modal"
              class="grow"
              tagClass="grow click-action"
              value={{ activeIndex: index, experienceZoneData: this.experienceZoneData }}
            >
              <bs-experience-zone-card
                class="teaser-card"
                singular={this.parsedExperienceZoneData.stories.length === 1}
                cardTitle={story.storyTitle}
                cardImage={story.storyImage}
                video={story.screens[0].layout === ExperienceZoneLayout.Video}
              />
            </bs-click-action>
          ))}
        </div>
      </Host>
    );
  }
}
